import React from 'react'; 

const About = (props) => {
    return (
        <div className="row about-us" id='about'>
            <div className="col-md-8 ml-auto mr-auto">
            <h2 className="title">Despre Noi</h2>
            <h5 className="description">Din 2017 oferim servicii de reparatii pentru aparatura electronica dupa care in functie de repetabilitatea defectiunilor in timp se poate opta pentru echipamente inteligente de protectie care pot inlatura cauza defectelor. </h5>
            <h5 className="description">Activitatea firmei a inceput cu o analiza amanuntita a situatiilor cu probleme, care pana la data respectiva activitatea de services se rezuma la inlocuirea piesei defecte in marea majoritate a cazurilor. In urma experientei acumulate in decursul anilor am ajuns la aceasta concluzie. De competitori ne separam prin calitatea serviciilor </h5>
            </div>
            <div className="col-md-4">
                <div className="about-us-pic"></div>
            </div>
        </div>
    );
}

export default About;


